<template>
    <!--登录组件-->
    <div class="container">
      
        <!--提示框-->
        <div class="tipslogin">
          <div class="tipsimg">
            <span>注册用户</span>
            <img src="../../assets/register.gif" alt="">
          </div>
          <span @click="returnhome" class="closeSpan">登录</span>
        </div>
        <!--广告图片-->
        <div class="imgDiv">
            <img src="../../assets/music.png" alt="" class="logo">
        </div>
        <!--操作区域-->
        <div class="operateDiv">
          <!--账号-->
          <mt-field :placeholder="accountPlaceholder" v-model="uname" class="myinput"></mt-field>
          <!--密码-->
          <mt-field type="password" placeholder="请输入密码" v-model="upwd" class="input"></mt-field>
          <!--登录-->
          <div>
            <mt-button size="large" class="mybutton" @click="register">注册</mt-button>
          </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      
      uname: "",
      upwd: "",
      accountPlaceholder: "请输入用户名"
    };
  },
  methods: {
    register() {
      // console.log("1243");
      // 1.完成用户登录
      // 2.先获得用户输入的用户名   3~10
      var n = this.uname;
      //3. 获得用户输入的密码       3~8
      var p = this.upwd;
      // 4.创建2个正则表达式
      var nreg = /^[a-zA-Z0-9]{3,10}$/;
      var preg = /^[a-zA-Z0-9]{6,8}$/;
      // 5.判断用户名格式不正确  提示出错
      if (!nreg.test(n)) {
        this.$toast("用户名格式不正确");
        return;
      }
      // 6.判断密码格式不正确    提示出错
      if (!preg.test(p)) {
        this.$messagebox("密码错误");
        return;
      }
      // 7.将用户名和密码发送  ajax请求
      var url = "register";
      var data = `uname=${n}&upwd=${p}`;
      this.axios.post(url, data).then(result => {
        // 8.并且获取服务器返回结果
        if (result.data.code == 1) {
          // 10.登录成功，返回首页
          this.$messagebox("消息", "注册成功");
          // open("/home","_blank");
          // 自动跳转到某页
          this.$router.push("/login");
          // window.open("cart.vue");
        } else {
          // 9.返回失败，提示
          this.$messagebox("消息", result.data.msg);
        }
      })
    },
    returnhome() {
      this.$router.push("/login");
    }
  }
};
</script>
<style scoped>
/* 你需要登录才能继续仿问的布局样式 */
.tipslogin {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 52px;
  background: rgb(238, 238, 238);
  padding-left: 20px;
  padding-right: 20px;
}
.tipsimg {
  display: flex;
  align-items: center;
}
/* 半闭样式 */
.closeSpan {
  color: rgb(153, 153, 153);
}
/* 图片 logo 根样式 */
.imgDiv {
  display: flex;
  justify-content: center;
}
.imgDiv > img {
  width: 100px;
  height: 100px;
}
/* 图片样式 */
.logo {
  width: 80px;
  height: 80px;
}
/* 上面的线隐藏 */
.mint-cell-wrapper {
  background-image: linear-gradient(180deg, #fff, #fff 0%, transparent 0%);
}
/* 输入框底边框样式 */
.mint-cell-wrapper {
  border-bottom: 1px solid #ff5000;
}

/* 修改按钮按钮下的默认颜色 */
.mint-button::after {
  background-color: transparent;
}
/* 操作区域的样式*/
.operateDiv {
  padding-left: 20px;
  padding-right: 20px;
}
/* 输入框边距 */
.myinput {
  margin-top: 30px;
}
/* 获取短信验证码字体颜色 */
.getCode {
  color: lightgreen;
  font-size: 17px;
  border-left: 1px solid #b5b5b5;
  padding-left: 7px;
}
/* 免费注册根样式 */
.registerDiv {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* 免费注册字体样式*/
.registerDiv span {
  color: #555;
}
/* 登录短信验证按钮边距 */
.mybutton {
  margin-top: 20px;
}
/* 修改默认 butoon 的样式 达到和淘宝登录一样 */
.mint-button--default {
  background: -webkit-linear-gradient(left, #ff9000, lightgreen) no-repeat;
  border-radius: 25px;
  color: #fff;
  height: 48px;
}
/* 同上 */
.mint-button--primary {
  border-radius: 25px;
  height: 48px;
  background-color: transparent;
  border: 1px solid #ff5000;
  color: lightgreen;
}
/* 修改默认弹框的样式，达到基本和淘宝的效果一样 */
.mint-msgbox {
  border-radius: 8px;
  width: 70%;
}
/* 弹出框内容样式 */
.mint-msgbox-content {
  min-height: 50px;
  font-size: 18px;
}
/* 弹出内容居中 */
